class Pool {
    constructor(size) {
        this.$_size = size || 3000
        this.$_cache = []
    }

    /**
     * 
     */
    add(object) {
        if (this.$_cache.length >= this.$_size) return

        this.$_cache.push(object)
    }

    /**
     * 
     */
    get() {
        return this.$_cache.pop()
    }
}

module.exports = Pool
