const Node = require('./node')
const tool = require('../util/tool')
const Pool = require('../util/pool')
const cache = require('../util/cache')

const pool = new Pool()

class TextNode extends Node {
    /**
     * 
     */
    static $$create(options, tree) {
        const config = cache.getConfig()

        if (config.optimization.textMultiplexing) {
            //  text 
            const instance = pool.get()

            if (instance) {
                instance.$$init(options, tree)
                return instance
            }
        }

        return new TextNode(options, tree)
    }

    /**
     *  $$init 
     */
    $$init(options, tree) {
        options.type = 'text'

        super.$$init(options, tree)

        this.$_content = options.content || ''
    }

    /**
     *  $$destroy 
     */
    $$destroy() {
        super.$$destroy()

        this.$_content = ''
    }

    /**
     * 
     */
    $$recycle() {
        this.$$destroy()

        const config = cache.getConfig()

        if (config.optimization.textMultiplexing) {
            //  text 
            pool.add(this)
        }
    }

    /**
     * 
     */
    $_triggerParentUpdate() {
        if (this.parentNode) this.parentNode.$$trigger('$$childNodesUpdate')
    }

    /**
     *  dom 
     */
    get $$domInfo() {
        return {
            nodeId: this.$_nodeId,
            pageId: this.$_pageId,
            type: this.$_type,
            content: this.$_content,
        }
    }

    /**
     * 
     */
    get nodeName() {
        return '#text'
    }

    get nodeType() {
        return Node.TEXT_NODE
    }

    get nodeValue() {
        return this.textContent
    }

    set nodeValue(value) {
        this.textContent = value
    }

    get textContent() {
        return this.$_content
    }

    set textContent(value) {
        value += ''

        this.$_content = value
        this.$_triggerParentUpdate()
    }

    get data() {
        return this.textContent
    }

    set data(value) {
        this.textContent = value
    }

    cloneNode() {
        return this.ownerDocument.$$createTextNode({
            content: this.$_content,
            nodeId: `b-${tool.getId()}`, //  b- 
        })
    }
}

module.exports = TextNode
